<template>
  <div class="auth-add-wrapper">
    <el-form :model="formAdd" ref="form_add" :rules="rules" label-width="80px" class="form-auth-add">
      <el-form-item label="" prop="username">
        <el-input v-model="formAdd.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="psd">
        <el-input v-model="formAdd.psd" autocomplete="new-password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="repsd">
        <el-input v-model="formAdd.repsd" autocomplete="new-password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleName">
        <el-select v-model="formAdd.roleName" placeholder="">
          <el-option label="" value="admin"></el-option>
          <el-option label="" value="user"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="btn-wrapper">
        <el-button type="primary" @click="onSubmit" class="add-auth" plain></el-button>
        <el-button type="primary" @click="onClose" class="add-auth" plain></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { addRole } from '@/service/role'
import cloneDeep from 'lodash/cloneDeep'
export default {
  name: 'add-role',
  data () {
    var repsdfn = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.formAdd.psd) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      formAdd: {
        username: '',
        roleName: '',
        psd: '',
        repsd: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        psd: [
          { required: true, message: '', trigger: 'blur' }
        ],
        repsd: [
          { validator: repsdfn, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    closeDialog () {
      this.$refs['form_add'].resetFields()
      this.$emit('FormVisible')
    },
    onSubmit () {
      this.$refs['form_add'].validate(valid => {
        if (valid) {
          let data = cloneDeep(this.formAdd)
          delete data.repsd
          addRole(data).then(
            res => {
              if (res.data && res.data.success) {
                this.$message('')
                this.$refs['form_add'].resetFields()
              }
            }
          )
        } else {
          return false
        }
      })
    },
    onClose () {
      this.$refs['form_add'].resetFields()
    }
  }
}
</script>
<style>
  .auth-add-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .el-button.add-auth {
    width: 40%;
    box-sizing: border-box;
  }
  .auth-add-wrapper .back {
    width: 100%;
    margin-bottom: 40px;
    padding-left: 30%;
    /* position: fixed;
    top: 20px;
    left: 300px; */
  }
  .auth-add-wrapper .btn-wrapper {
    text-align: center;
  }
  .form-auth-add {
    width: 40%;
  }
</style>



